<template>

   <div class="note  in-trial" :class="{focus :focusEdit===true ,'active-on-small':  idEditorActiveSmall==idEditor}" v-click-outside="hideNote">
      <div class="note-inner-wrapper">
         <div class="editor-wrapper pell-content" :class="classNote">
            <quill-editor class="editor"
                          ref="editor"
                          :value="content"
                          v-on:input="changeContent"
                          :options="editorOption"
                          @blur="onEditorBlur($event)"
                          @focus="onEditorFocus($event)"
                          @ready="onEditorReady($event)" />
            <div :id="this.idTooolBar" class="menu pell-actionbar ql-toolbar ql-snow">
               <button class="item ql-bold pell-button" type="button">

                                   </button>
               <button class="item ql-italic pell-button" type="button">

                                   </button>
               <button class="item ql-underline pell-button" type="button">

                                   </button>
               <button class="item ql-strike pell-button" type="button">

                                   </button>
               <button class="item ql-list pell-button" value="bullet" type="button">

                                   </button>
               <!-- <button class="item ql-link pell-button" type="button">

                                   </button> -->
            </div>
            <!-- <div class="create-link-container" style="transform: scale(1); top: 10px; left: 0px;">
                                  <span class="close-container">
                                     <span class="cross">x</span>
                                  </span><div class="wrapper-container">
                                     <div class="inner-container">
                                        <div class="edit-link-container"><div class="text-link-container">
                                           <div class="label-text">Website Link</div><input class="text-link-input">
                                        </div><div class="button-container"><span class="button">Done</span></div></div></div></div></div> -->
         </div>
         <div class="note-download-container">
            <span class="menu-button" v-on:click="expandItem()" v-click-outside="hide">
                     <span class="circle"><span class="arrow-down"></span></span>
            </span>
            <transition name="bounce">
               <div class="note-menu-container" style="transform: scale(1);" v-show="noteMenu">
                  <div class="main-container">
                     <div class="item-container">
                        <!-- <span class="icon lnr lnr-trash"></span> -->
                        <font-awesome-icon icon="trash" />
                        <span class="delete-note item-text">Clear</span>
                     </div>
                     <div class="item-container hide-item">

                        <font-awesome-icon icon="eye-slash" />
                        <span class="delete-note item-text">Hide</span>
                     </div>
                     <div class="item-container">
                        <font-awesome-icon icon="download" />
                        <span class="download-note item-text">Export as text</span>
                     </div>
                     <div class="item-container">
                        <font-awesome-icon icon="print" />
                        <span class="download-note item-text">Print</span>
                     </div>
                  </div>
                  <div class="arrow-up-container">
                     <div class="arrow-down"></div>
                  </div>
               </div>
            </transition>
         </div>
      </div>

   </div>

</template>
<script>

   import ClickOutside from 'vue-click-outside'
   export default {
      name: 'quill-example-nuxt',
      props: ['idTooolBar', 'content', 'tabActive', 'idEditor', 'textSizeActive','fontFamilyActive','idEditorActiveSmall'],
      data() {
         return {
            noteMenu: false,
            focusEdit: false,
            editorOption: {
               // Some Quill options...
               theme: 'snow',
               modules: {
                  toolbar: '#' + this.idTooolBar
                  // toolbar: false
                  // toolbar: [
                  //    ['bold', 'italic', 'underline', 'strike'],
                  //    ['blockquote', 'code-block'],
                  //    [{ list: 'ordered' }, { list: 'bullet' }]
                  // ]
               }
            }
         }
      },
      computed: {
         classNote: function() {
            var c = this.tabActive % 3
            if (c == 0) c = 1
            return 'sn-' + c + '-' + this.idEditor
         }
      },

      watch: {
         textSizeActive: function() {
            this.$refs.editor.quill.container.style.fontSize = this.textSizeActive.fontSize
         },
         fontFamilyActive: function() {
            this.$refs.editor.quill.container.style.fontFamily = this.fontFamilyActive.fontFamily
         }
      },
      methods: {
         // setUp()Ơ
         changeContent(e) {
            // console.log();
            // console.log(e);
            this.$emit('handelchangeContent', { idEditor: this.idEditor, content: e })

            // this.$refs.editor.quill.container.style.fontFamily = 'Comic Sans Ms'
         },
         onEditorBlur() {
            // console.log('editor blur!', editor)
            // this.focusEdit = false;
         },
         onEditorFocus() {
            // console.log('editor focus!', editor)
            this.focusEdit = true
         },
         onEditorReady() {
            // console.log('editor ready!', editor)
            this.$refs.editor.quill.container.style.fontSize = this.textSizeActive.fontSize
           this.$refs.editor.quill.container.style.fontFamily = this.fontFamilyActive.fontFamily
         },
         expandItem() {
            this.noteMenu = !this.noteMenu
         },
         hide() {
            this.noteMenu = false
         },
         hideNote() {
            this.focusEdit = false
         }
      },
      directives: {
         ClickOutside
      }
   }

</script>
<style  scoped>

   .bounce-enter-active {
      animation: bounce-in 0.5s;
   }
   .bounce-leave-active {
      animation: bounce-in 0.5s reverse;
   }
   @keyframes bounce-in {
      0% {
         transform: scale(0);
      }
      50% {
         transform: scale(1.2);
      }
      100% {
         transform: scale(1);
      }
   }

</style>
