<template>

   <div class="folder-1  folder" :class="{active: tabActive===item.idTab}" @click="handleTabClick()" v-click-outside="hidedropdownTab">
      <div class="folder-inner-container" style="transform: scale(1);"><span class="text">{{nameTabFormat}}</span>
         <transition name="bounce">
            <span v-if="tabActive===item.idTab" class="folder-action">
                <span class="icon-container" style="transform: scale(1);" @click="handedropdownTab()">
                <span class="circle"><span class="arrow-down"></span> </span>
            </span>
            <transition name="bounce">
               <div class="folder-dropdown" style="transform: scale(1);" v-show="dropdownTab">
                  <div class="arrow-up-container"><span class="arrow-up"></span>
                  </div>
                  <div class="main-container">
                     <div class="name-container">
                        <div class="label-text"> Name </div>
                        <div class="input-container">
                           <input type="text" v-model="item.message">
                        </div>
                     </div>
                     <div class="notes-toggle-container">
                        <div class="label-text">Show / Hide notes</div>
                        <div class="live-message">Currently showing<span class="visible-count">{{item.listEditor.length }}</span>notes</div>
                        <div class="small-notes-container">
                           <div class="small-note" v-for="(item,index) in dataItem" :key="index" :class="{tick:item}"
                           v-on:click="checkTick(index)">
                              <span class="circle">✓</span>
                           </div>
                           <!-- <div class="small-note tick"><span class="circle">✓</span>
                                 </div>
                                 <div class="small-note tick"><span class="circle">✓</span>
                                 </div> -->
                        </div>
                     </div>
                  </div>
               </div>
            </transition>
            </span>
         </transition>
      </div>

   </div>

</template>
<script>

   import ClickOutside from 'vue-click-outside'
   export default {
      name: '',
      props: ['item', 'tabActive','appSizeActive'],
      data: () => ({
         dropdownTab: false,
         dataItem: [
             false,
             false,
             false,
         ]
      }),
      computed:{
         nameTabFormat(){
            if (this.appSizeActive == 'Tiny') {
               return this.item.message.substr(0, 1)
            }else{
               return this.item.message;
            }
         }
      },
      methods: {
         checkTick(e) {

            if(this.checkItem(e)==true){
               this.$set(this.dataItem, e, !this.dataItem[e]);
               var a =[];
               this.dataItem.forEach((value, index) => {
                   if(value==true) a.push(index+1);
               });
               this.$emit('handleChangeTickTab', a);
            }

         },
         checkItem(e){
            var countTrue = 0;
            for(var i= 0; i<3 ;i++){
               if(this.dataItem[i]==true) {
                  countTrue++;
               }
            }
            if(countTrue>1) {
                return true;
               // console.log(countTrue)
            }else{
               var vtriTrue;
               for(var j= 0; j<3 ;j++){
                  if(this.dataItem[j]==true) {
                     vtriTrue =j;
                     break;
                  }
               }

               if(vtriTrue ==e){
                  return false;
               }else{
                  return true;
               }
               // return true;
            }
         },
         handleTabClick() {
            this.$emit('handleTabClick', this.item.idTab)
            // console.log(this.item.idTab);
         },
         handedropdownTab() {
            this.dropdownTab = !this.dropdownTab
         },
         hidedropdownTab() {
            this.dropdownTab = false
         }
      },
      directives: {
         ClickOutside
      },
      created() {
         //do something after creating vue instance
         this.dataItem[0] = this.item.listEditor.includes(1);
         this.dataItem[1] = this.item.listEditor.includes(2);
         this.dataItem[2] = this.item.listEditor.includes(3);
      }
   }

</script>
<style scoped>

   .bounce-enter-active {
      animation: bounce-in 0.5s;
   }
   .bounce-leave-active {
      /* animation: bounce-in .10s reverse; */
   }
   @keyframes bounce-in {
      0% {
         transform: scale(0);
      }
      50% {
         transform: scale(1.2);
      }
      100% {
         transform: scale(1);
      }
   }

</style>
