            if(data==null){
               localStorage.setItem('data',JSON.stringify(this.$data));
            }else{
               if(data.test !=0 || data.test==undefined){
                  localStorage.setItem('data',JSON.stringify(this.$data));
               }else{
                  Object.assign(this.$data,data);
                  // console.log(data.test)
               }
            }
         } else {
