<script>
import Tab from './Tab.vue'
import ClickOutside from 'vue-click-outside'
export default {
   name: '',
   props: ['items', 'tabActive', 'themeActive', 'appSizeActive','textSizeActive','fontFamilyActive'],
   data() {
      return {
         setting: false,
         show_themes: false,
         show_font: false,
         show_app_size: false,
         show_text_size: false,
      }
   },
   components: {
      Tab
   },
   computed: {
      classOption: function() {
         if (this.appSizeActive == 'Big' || this.appSizeActive == 'Medium') {
            return "default"
         }
         if (this.appSizeActive == 'Compact') {
            return "compact"
         }
         if (this.appSizeActive == 'Tiny') {
            return "small"
         }
         return "default"
      },

   },
   methods: {
      resetSetting() {
         this.show_themes = false,
            this.show_font = false,
            this.show_app_size = false,
            this.show_text_size = false
      },
      handleTabClick(e) {
         this.$emit('handleTabClick', e)
      },
      handleChangeTickTab(e) {
         // console.log(e);
         this.$emit('handleChangeTickTab', e);
      },
      handleSettingClick() {
         this.resetSetting();
         this.setting = !this.setting
      },
      closeSetting() {
         this.setting = false
      },
      hideSetting() {
         this.setting = false;
      },
      handleClickTab(e) {
         // this.resetSetting();
         if (e == 'show_themes' && this.show_themes == true) {
            this.show_themes = false;
         } else {
            if (e == 'show_themes') {
               this.resetSetting()
               this.show_themes = true
            }
         }
         if (e == 'show_font' && this.show_font == true) {
            this.show_font = false;
         } else {
            if (e == 'show_font') {
               this.resetSetting()
               this.show_font = true
            }
         }
         if (e == 'show_app_size' && this.show_app_size == true) {
            this.show_app_size = false;
         } else {
            if (e == 'show_app_size') {
               this.resetSetting()
               this.show_app_size = true
            }
         }
         if (e == 'show_text_size' && this.show_text_size == true) {
            this.show_text_size = false;
         } else {
            if (e == 'show_text_size') {
               this.resetSetting()
               this.show_text_size = true
            }
         }
      },

   },

   directives: {
      ClickOutside
   },
}
</script>
